* {
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

h2 {
  text-align: center;
  color: #444;
}

.board-grid {
  --board-width: 660px;
  --gutter-size: 10px;
  --cell-side: calc((var(--board-width)  - (var(--gutter-size) * 4)) / 5);
  --unselected-background: white;
  --selected-background: #eee;
  --selected-winning-background: #abffd5;
  width: var(--board-width);
  grid-column-gap: var(--gutter-size);
  grid-row-gap: var(--gutter-size);
  -webkit-user-select: none;
  user-select: none;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  place-items: center;
  margin: 0 auto;
  display: grid;
}

.board-grid .cell {
  word-wrap: break-word;
  width: var(--cell-side);
  height: var(--cell-side);
  text-align: center;
  background-color: var(--unselected-background);
  filter: drop-shadow(2px 4px 3px #ddd);
  border-radius: 5px;
  outline: 1px solid #d3d3d3;
  display: block;
  position: relative;
}

.board-grid .cell:hover:not(.bonus) {
  transition: transform .2s;
  transform: translateY(-5px);
}

.board-grid .cell.selected {
  background-color: var(--selected-background);
}

.board-grid .cell.winning {
  background-color: var(--selected-winning-background);
}

.board-grid .cell .word {
  pointer-events: none;
  width: auto;
  padding: 5px;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

#vocabularySelector {
  text-align: center;
  margin: 50px;
}

#vocabularySelector textarea {
  width: 100%;
  min-height: 100px;
}

